* {
  font-family: 'Poppins', sans-serif;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.btn_custom_primary {
  background-color: #6C63FF;
  color: white;
}

.footer__main {
  background-color: #6C63FF;
  padding: 30px;
}

.footer__bottom {
  padding: 8px;
  background-color: #665CFF;
}

.frame {
  position: relative;
  overflow: hidden;
}

.background__loading {
  position: relative;
  overflow: hidden;
  background-color: red;
  width: 100%;
  height: 100%;
}
